	module.exports = {"data":{"markdownRemark":{"html":"<p>When a condition for an <code>if</code> statement is true, the block of code following it is executed. What about when that condition is false? Normally nothing would happen. With an <code>else</code> statement, an alternate block of code can be executed.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>if (num > 10) {\n  return \"Bigger than 10\";\n} else {\n  return \"10 or Less\";\n}</code></pre>\n      </div>","fields":{"slug":"/certificates/introducing-else-statements/"},"frontmatter":{"title":"Introducing Else Statements"}}},"pathContext":{"slug":"/certificates/introducing-else-statements/"}}

